<template>
  <el-drawer
    title=""
    v-model="modelValue"
    direction="rtl"
    @open="drawerOpenHandler"
    @close="$emit('update:modelValue', false)"
  >
    <div class="envVariables">
      <textarea ref="envVariablesEditor">
{
  "${javaPath}": "src/main/java",
  "${author}": "lvws",
  "${date}": "2021-06-20",
  "${package}": "org.jaguar.modules.codegen",
  "${packagePath}": "org/jaguar/modules/codegen",
  "${module}": "codegen",
  "${entity}": "CodeTemplate",
  "${entityName}": "codeTemplate",
  "${dateTimeScore}": 0,
  "${table}": {
    "createTime": "2021-06-19 20:45:13",
    "engine": "InnoDB",
    "tableComment": "",
    "tableName": "jaguar_modules_codegen_code_template"
  },
  "${columns}": [{
    "columnComment": "",
    "columnName": "code_template_type",
    "columnType": "varchar(20)",
    "dataType": "varchar",
    "fieldName": "codeTemplateType",
    "filedType": "String"
  }, {
    "columnComment": "",
    "columnName": "code_template_file",
    "columnType": "text",
    "dataType": "text",
    "fieldName": "codeTemplateFile",
    "filedType": "String"
  }, {
    "columnComment": "",
    "columnName": "code_template_version",
    "columnType": "int(11)",
    "dataType": "int",
    "fieldName": "codeTemplateVersion",
    "filedType": "Integer"
  }]
}
      </textarea>
    </div>
  </el-drawer>
</template>
<script>
import CodeMirror from 'codemirror'

export default {
  name: 'EnvVariable',
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      required: true,
    },
  },
  data() {
    return {
      envVariablesEditor: null,
      cmOptions: {
        mode: 'application/json', //Java
        smartIndent: true, // 
        autoCloseBrackets: true, // 
        tabSize: 4, // tab
        theme: 'dracula', //
        lineNumbers: true, //
        lineWrapping: true, //
        styleActiveLine: true, //line
        matchBrackets: true, //
        option: {
          autofocus: true,
        },
        readOnly: true,
      },
    }
  },
  methods: {
    drawerOpenHandler() {
      if (this.envVariablesEditor) return

      this.$nextTick(() => {
        this.envVariablesEditor = CodeMirror.fromTextArea(
          this.$refs.envVariablesEditor,
          this.cmOptions
        )
      })
    },
  },
}
</script>
