<template>
  <el-dialog
    v-model="dialog.visible"
    title=""
    @open="openHandler"
    @close="closeHandler"
    :close-on-click-modal="false"
    append-to-body
    center
    fullscreen
  >
    <el-form
      ref="dataForm"
      :model="entity"
      :rules="formRules"
      label-position="left"
      label-width="auto"
      label-suffix=""
      class="codeTemplate"
    >
      <el-row :gutter="20">
        <el-col :span="4">
          <el-form-item prop="codeTemplateName" label="">
            <el-input
              v-model="entity.codeTemplateName"
              placeholder=""
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item prop="tableName" label="">
            <el-input v-model="entity.tableName" disabled />
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item prop="tablePrefix" label="">
            <el-input
              v-model="entity.tablePrefix"
              placeholder=""
              @input="
                () => {
                  previewFileName()
                  preview()
                }
              "
            />
          </el-form-item>
        </el-col>

        <el-col :span="4">
          <el-form-item prop="parentPackage" label="">
            <el-input
              v-model="entity.parentPackage"
              placeholder=""
              @input="
                () => {
                  previewFilePath()
                  preview()
                }
              "
            />
          </el-form-item>
        </el-col>

        <el-col :span="4">
          <el-form-item prop="moduleName" label="">
            <el-input
              v-model="entity.moduleName"
              placeholder=""
              @input="
                () => {
                  previewFilePath()
                  preview()
                }
              "
            />
          </el-form-item>
        </el-col>

        <el-col :span="4">
          <el-form-item prop="author" label="">
            <el-input
              v-model="entity.author"
              placeholder=""
              @input="preview()"
            />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="6">
          <el-form-item prop="fileName" label="">
            <el-input
              v-model="entity.fileName"
              placeholder=""
              clearable
              @blur="previewFileName"
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="">
            <el-input v-model="entity.realFileName" readonly />
          </el-form-item>
        </el-col>

        <el-col :span="6">
          <el-form-item prop="filePath" label="">
            <el-input
              v-model="entity.filePath"
              placeholder=""
              clearable
              @blur="previewFilePath"
            />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item prop="filePath" label="">
            <el-input v-model="entity.realFilePath" readonly />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item prop="codeTemplateFile" label="">
            <textarea ref="codeEditor" />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="">
            <textarea ref="previewCodeEditor" />
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>

    <env-variable v-model="drawerVisible" />

    <template #footer>
      <el-button size="small" @click="drawerVisible = true"></el-button>
      <el-button size="small" @click="dialog.visible = false"> </el-button>
      <el-button type="primary" size="small" @click="saveHandler">
         
      </el-button>
    </template>
  </el-dialog>
</template>
<script>
import { modify } from '@/api/codegen/codeTemplate'
import { preview, previewFileName, previewFilePath } from '@/api/codegen'
import EnvVariable from './EnvVariable'

import CodeMirror from 'codemirror'

import 'codemirror/lib/codemirror.css'
import 'codemirror/theme/dracula.css' // thememonokai
// 
import 'codemirror/theme/monokai.css'
import 'codemirror/theme/base16-light.css'

// keyMap
import 'codemirror/mode/clike/clike.js'
import 'codemirror/mode/sql/sql.js'
import 'codemirror/mode/xml/xml.js'
import 'codemirror/mode/javascript/javascript.js'

import 'codemirror/addon/edit/closebrackets.js'
import 'codemirror/addon/selection/active-line'

require('codemirror/addon/fold/foldcode.js')
require('codemirror/addon/fold/foldgutter.js')
require('codemirror/addon/fold/brace-fold.js')
require('codemirror/addon/fold/xml-fold.js')
require('codemirror/addon/fold/indent-fold.js')
require('codemirror/addon/fold/markdown-fold.js')
require('codemirror/addon/fold/comment-fold.js')

import 'codemirror/addon/selection/mark-selection.js'

import 'codemirror/addon/hint/show-hint.js'
import 'codemirror/addon/hint/sql-hint.js'
import 'codemirror/addon/hint/show-hint.css'
import 'codemirror/addon/hint/javascript-hint.js'

import 'codemirror/addon/scroll/annotatescrollbar.js'
import 'codemirror/addon/search/matchesonscrollbar.js'
import 'codemirror/addon/search/match-highlighter.js'

import 'codemirror/addon/edit/matchbrackets.js'
import 'codemirror/addon/comment/comment.js'
import 'codemirror/addon/dialog/dialog.js'
import 'codemirror/addon/dialog/dialog.css'
import 'codemirror/addon/search/searchcursor.js'
import 'codemirror/addon/search/search.js'
import 'codemirror/keymap/sublime.js'

export default {
  name: 'TemplateModify',
  props: {
    dialog: {
      type: Object,
      required: true,
    },
  },
  emits: ['success'],
  data() {
    return {
      entity: {},
      formRules: {
        codeTemplateName: [
          { required: true, message: '', trigger: 'blur' },
        ],
        codeTemplateFile: [
          { required: true, message: '', trigger: 'blur' },
        ],
        parentPackage: [
          { required: true, message: '', trigger: 'blur' },
        ],
        moduleName: [
          { required: true, message: '', trigger: 'blur' },
        ],
        author: [{ required: true, message: '', trigger: 'blur' }],
        fileName: [
          { required: true, message: '', trigger: 'blur' },
        ],
        filePath: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },
      cmOptions: {
        mode: 'text/x-java', //Java
        smartIndent: true, // 
        autoCloseBrackets: true, // 
        tabSize: 4, // tab
        theme: 'dracula', //
        lineNumbers: true, //
        lineWrapping: true, //
        styleActiveLine: true, //line
        matchBrackets: true, //
        option: {
          autofocus: true,
        },
      },
      codeEditor: null,
      previewCodeEditor: null,
      previewTimeout: null,
      drawerVisible: false,
    }
  },
  components: {
    EnvVariable,
  },
  methods: {
    openHandler() {
      this.entity = Object.assign(
        {
          dataSourceName: 'master',
          codeTemplateFile: '',
          tableName: 'jaguar_codegen_code_template',
          tablePrefix: 'jaguar_codegen_',
          parentPackage: 'top.wilsonlv.jaguar',
          moduleName: 'codegen',
          author: 'lvws',
          fileName: '',
          filePath: '',
          realFileName: '',
          realFilePath: '',
        },
        this.dialog.entity
      )

      this.$nextTick(() => {
        let codeEditor = this.$refs.codeEditor
        codeEditor.value = this.entity.codeTemplateFile

        this.codeEditor = CodeMirror.fromTextArea(
          codeEditor,
          Object.assign({ readOnly: false }, this.cmOptions)
        )

        this.codeEditor.on('change', (coder) => {
          this.entity.codeTemplateFile = coder.getValue()
          this.preview()
        })

        this.preview(true)
        this.previewFileName()
        this.previewFilePath()
      })
    },
    closeHandler() {
      this.entity = {}
      this.codeEditor.toTextArea()
      this.previewCodeEditor.toTextArea()
    },
    previewFileName() {
      if (this.entity.fileName) {
        previewFileName({
          tableName: this.entity.tableName,
          tablePrefix: this.entity.tablePrefix,
          fileName: this.entity.fileName,
        }).then((res) => {
          this.entity.realFileName = res.data.data
        })
      }
    },
    previewFilePath() {
      if (
        this.entity.parentPackage &&
        this.entity.moduleName &&
        this.entity.filePath
      ) {
        previewFilePath({
          tableName: this.entity.tableName,
          tablePrefix: this.entity.tablePrefix,
          parentPackage: this.entity.parentPackage,
          moduleName: this.entity.moduleName,
          filePath: this.entity.filePath,
        }).then((res) => {
          this.entity.realFilePath = res.data.data
        })
      }
    },
    preview(inmediately = false) {
      if (!this.entity.codeTemplateFile) {
        let codeEditor = this.$refs.previewCodeEditor
        codeEditor.value = ''

        this.previewCodeEditor = CodeMirror.fromTextArea(
          this.$refs.previewCodeEditor,
          Object.assign({ readOnly: true }, this.cmOptions)
        )
        return
      }

      let previw = () => {
        preview(this.entity).then((res) => {
          let scrollTop
          if (this.previewCodeEditor) {
            scrollTop = this.previewCodeEditor.display.scroller.scrollTop
            this.previewCodeEditor.toTextArea()
          } else {
            scrollTop = 0
          }

          let codeEditor = this.$refs.previewCodeEditor
          codeEditor.value = res.data.data

          this.previewCodeEditor = CodeMirror.fromTextArea(
            codeEditor,
            Object.assign({ readOnly: true }, this.cmOptions)
          )

          this.previewCodeEditor.display.scroller.scrollTop = scrollTop
          this.previewTimeout = null
        })
      }

      if (inmediately) {
        previw()
      } else {
        if (this.previewTimeout) clearTimeout(this.previewTimeout)
        this.previewTimeout = setTimeout(() => {
          previw()
        }, 1500)
      }
    },
    saveHandler() {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          modify(this.entity).then(() => {
            this.dialog.visible = false
            this.$message({
              showClose: true,
              message: '',
              type: 'success',
            })
            this.$emit('success')
          })
        }
      })
    },
  },
}
</script>
<style lang="scss">
.codeTemplate {
  .CodeMirror {
    height: 640px;

    .CodeMirror-line {
      line-height: 28px;
    }
  }
}
.envVariables {
  .CodeMirror {
    height: 800px;
  }
}
</style>
